class SortingHelpers{

}

export default SortingHelpers;

SortingHelpers.prototype.findMax = function findMax(items, ref){
    let max;
    let i;

    items.forEach(item => {
        let val = Number(valueOf(item, ref));

        if(max == null){
            max = val;
            i = item;
        } else {
            if(max < val){
                max = val;
                i = item;
            }
        }
    });

    return i;
}

SortingHelpers.prototype.findMin = function findMin(items, ref){
    let min;
    let i = 0;

    items.forEach(item => {
        let val = Number(valueOf(item, ref));
        
        if(min == null){
            min = val;
            i = item;
        } else {
            if(min > val){
                min = val;
                i = item;
            }
        }
    });

    return i;
}

SortingHelpers.prototype.quickSort = function quickSort(items, left, right, ref){
    var index;
    if (items.length > 1) {
        index = partition(items, left, right, ref); //index returned from partition
        if (left < index - 1) { //more elements on the left side of the pivot
            quickSort(items, left, index - 1, ref);
        }
        if (index < right) { //more elements on the right side of the pivot
            quickSort(items, index, right, ref);
        }
    }
    
    return items;
}

function partition(items, left, right, ref) {
    var pivot   = items[Math.floor((right + left) / 2)], ref, //middle element
        l       = left, //left pointer
        r       = right; //right pointer
    while (l <= r) {
        while (valueOf(items[l], ref) < valueOf(pivot, ref)) {
            l++;
        }
        while (valueOf(items[r], ref) > valueOf(pivot, ref)) {
            r--;
        }
        if (l <= r) {
            swap(items, l, r); //sawpping two elements
            l++;
            r--;
        }
    }
    return l;
}

function swap(items, leftIndex, rightIndex){
    var temp = items[leftIndex];
    items[leftIndex] = items[rightIndex];
    items[rightIndex] = temp;
}

function valueOf(item, ref){
    let re = ref.split('/');
    let i = item;

    re.forEach(r => {
        i = i[r];
    })

    return i;
}