class TimeHelpers{

}

export default TimeHelpers;

TimeHelpers.prototype.sToHms = function(seconds){
    let time = {};

    let h = parseInt(seconds/3600);
    let m = parseInt((seconds - h*3600)/60);
    let s = seconds - h * 3600 - m * 60;
    
    h = h < 10? '0' + h : h;
    m = m < 10? '0' + m : m;
    s = s < 10? '0' + s : s;

    time = {
        h: h,
        m: m,
        s: s,
    }

    time.full = h + ':' + m + ':' + s;
    
    h = h == 0? '' : h + ':';
    m = m == 0? '' : m + ':';

    time.short = h + m + s;

    return time;
}