import React from 'react';
import {showAlert} from '../../Alert';
import SubmitDataHelpers from '../../helpers/SubmitDataHelpers';
import SortingHelpers from '../../helpers/SortingHelpers';
import CookieHelpers from '../../helpers/CookieHelpers';
import TimeHelpers from '../../helpers/TimeHelpers';

import CourseResultStyle from '../../../css/courses/CourseResultStyle';
import '../../../css/courses/CourseResult.css';

class CourseScan extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
    }

    componentDidMount(){
        this.renderResults();
        CourseResultStyle(this.props.course.checkPointNo);
    }

    componentDidUpdate(){
        CourseResultStyle(this.props.course.checkPointNo);
    }

    componentWillUnmount(){

    }

    render(){
        return (
            <div id="courseResult" className='courseResult px-sm-5 pt-4 pt-sm-5'>
                <h2 className="title mt-3 mb-4">Results</h2>
                {this.state.content}
            </div>
        );
    }

    renderResults(){
        let cookieHelpers = new CookieHelpers;

        let activeId = cookieHelpers.getCookie("activeId");

        let helpers = new SubmitDataHelpers;
        helpers.postData('/results/getResults/', 
        {
            event: this.props.course.event,
            type: this.props.course.type,
        }).then((res) => {
            //console.log(res); // JSON data parsed by `response.json()` call
            
            if(res.error){
                showAlert(res.error, 5000);
                //console.log(res.error);
            } else {
                let your = [];

                let sortingHelpers = new SortingHelpers;

                let highestScanNo = sortingHelpers.findMax(res, 'scanNo').scanNo;

                let tr = [];

                while (highestScanNo > 0){
                    let results = [];

                    res.forEach(r => {
                        if(r.scanNo == highestScanNo){
                            results.push(r);
                        }
                    })

                    let count = 0;
                    while(results.length > 0){
                        let minResult = sortingHelpers.findMin(results, 'timeTaken');
                        
                        let sortedRes = [];

                        res.forEach(r => {
                            if(r.username === minResult.username){
                                sortedRes.push(r);
                            }
                        });

                        sortedRes = sortingHelpers.quickSort(sortedRes, 0, sortedRes.length - 1, 'scanNo');
                        let checkpoint =[];
                        
                        sortedRes.forEach(r => {
                            checkpoint.push(
                                <div key={r.position + r.timeTaken + 'checkpoint'} className='checkpoint tooltips d-flex align-items-center'>
                                    <div className=''></div>
                                    <span className="tooltiptext px-3 py-3">
                                        Number of Scans: {r.scanNo}
                                        <br/>Score: {r.score}
                                        <br/>Time Taken: {(new TimeHelpers).sToHms(r.timeTaken).short}
                                        <br/>Speed: {(new TimeHelpers).sToHms(r.speed).short}
                                    </span>
                                </div>
                            );
                        });

                        for(let i = res.length -1; i >= 0; i--){
                            let r = res[i];
                            
                            if(r.username === minResult.username){
                                res.splice(res.indexOf(r), 1);
                                
                            }
                        }

                        results.splice(results.indexOf(minResult), 1);

                        let td = [];
                        td.push(
                            <div key={minResult.username + 'td'} id={minResult.username} className='td col-10'>
                                <div key={minResult.username + 'label'} className="header text-left py-3 px-3"><b>{minResult.username}</b></div>
                                <div key={minResult.username + 'bar'} className="bar col-12 d-flex align-items-center pb-3">{checkpoint}</div>
                                <div key={minResult.username + 'final'} className="finalResult col-12 d-flex justify-content-around pb-2">
                                    {this.props.course.type == 'Score'? 
                                    <div className='score'>
                                        {window.innerWidth > 576? 'Points: ' : 'P: '} {minResult.score}
                                    </div> : ''}
                                    <div className='timeTaken'>
                                        {window.innerWidth > 576? 'Time Taken: ' : 'T: '} {(new TimeHelpers).sToHms(minResult.timeTaken).short}
                                    </div>
                                    <div className='speed'>
                                        {window.innerWidth > 576? 'Average Speed: ' : 'AS: '} {(new TimeHelpers).sToHms(minResult.speed).short}
                                    </div>
                                </div>
                            </div>
                        );
                        
                        td.push(
                            <div key={minResult.position + 'td'} className='td col-2 pt-4'>
                                <h4>{minResult.position}</h4>
                                <span className='label'>{window.innerWidth > 576? 'Position' : 'Pos'}</span>

                            </div>
                        );
                        
                        tr.push(
                            <div key={minResult.position + 'tr'} className='tr col-12 row p-0 mx-0 border'>
                                {td}
                            </div>
                        );

                        if(your.length == 0 && activeId.indexOf(minResult.username) == 0){
                            your.push(
                                <div key={minResult.position + 'yourtr'} className='tr col-12 row p-0 mx-0'>
                                    {td}
                                </div>
                            );
                        }

                        count++;
                        if(count > 1000){
                            //console.log("end");
                            break;
                        }
                    }

                    highestScanNo--;
                }

                let content = [];

                content.push(
                    <div key="yourResult" id="yourResult" className='yourResult mb-2'>
                        {your}
                    </div>
                );

                content.push(
                    <div key='resultsTable' id="resultsTable" className='resultsTable'>
                        <div><b>All results</b></div>
                        <div className='table col-12 p-0 py-1 py-sm-2 border'>
                            <div className='tbody px-3 px-sm-4'>
                                {tr}
                            </div>
                        </div>
                    </div>
                )

                this.setState({content: content});
            }
        });
    }
}

export default CourseScan;